<template>
   <div class="content">
       <div class="box-blog" style="position: relative">
           <div class="img-head">
               <el-carousel :interval="5000" arrow="always">
                   <el-carousel-item >

                       <img src="../../assets/img/lun1.png" height="400" width="1200"/></el-carousel-item>
                   <el-carousel-item >

                       <img src="../../assets/img/lun2.jpg" height="400" width="1200"/></el-carousel-item>
                   <el-carousel-item >

                       <img src="../../assets/img/lun3.png" height="400" width="1200"/></el-carousel-item>
               </el-carousel>
           </div>

           <div style="display: inline-block; position: relative; top: -135px; margin-left: 20px; background: #fff; width: 500px; padding: 20px; border-radius: 10px" >
           </div>
         <div>
           <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" >
             <el-form-item label="姓名" prop="realName" style="width: 60%">
               <el-input type="text" v-model="ruleForm.realName" autocomplete="off" placeholder="请输入姓名"></el-input>
             </el-form-item>
             <el-form-item label="电话" prop="userPhone">
               <el-input type="password" v-model="ruleForm.userPhone" autocomplete="off"  placeholder="请输入电话"></el-input>
             </el-form-item>
             <el-form-item label="留言内容" prop="leaveMessage">
               <el-input v-model.number="ruleForm.leaveMessage" prop="name" type="textarea"></el-input>
             </el-form-item>
             <el-form-item>
               <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
               <el-button @click="resetForm('ruleForm')">重置</el-button>
             </el-form-item>
           </el-form>
         </div>
       </div>

       <foot></foot>
   </div>
</template>

<script>

    import foot from "../../components/common/foot"
    import {request} from '../../network/request'
    import {eduleavenmsginsert} from '@/api/user';
    export default {
        name: "blog",
        data() {
            return {
                activeName: 'second',
                blog: [],
                ruleForm: {
                realName: '',
                userPhone: '',
                leaveMessage: ''
              },
              rules: {
                realName: [
                  { required: true, message: '请输入姓名', trigger: 'blur' },
                  { min: 2, max: 64, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                ],
                userPhone: [
                  { required: true, message: '请输入电话', trigger: 'blur' },
                  { min: 7, max: 11, message: '长度在 7 到 11个字符', trigger: 'blur' }
                ],
                leaveMessage: [
                  { required: true, message: '请输入留言内容', trigger: 'blur' },
                  { min: 1, max: 255, message: '长度在 1 到 255 个字符', trigger: 'blur' }
                ],
              }
            };
        },
        methods: {
          submitForm(formName) {
            this.$refs[formName].validate((valid) => {
              eduleavenmsginsert(this.ruleForm).then((response)=>{
                if (response.data.code === 0){
                  this.$message.success("留言成功，欢迎多多指教！")
                  this.$router.go(0);
                }
              })
              // if (valid) {
              //   alert('submit!');
              // } else {
              //   console.log('error submit!!');
              //   return false;
              // }
            });
          },
          resetForm(formName) {
            this.$refs[formName].resetFields();
          },
            handleClick(tab, event) {
                console.log(tab, event);
            },

        },
        components: {
            foot
        },
        created() {

        }
    }
</script>

<style scoped>
    .content{
        width: 100%;
        height: 100%;
        background: #f5f6f7 !important;
    }
    .body-inner{
        width: 640px;
        background: #fff;
        border-radius: 10px;
        height: 1000px;
        overflow: auto;
    }
    .box-blog{
        width: 1200px;
        margin: 0 auto;

    }
    ::-webkit-scrollbar {
        width:5px;

    }

    /* 滚动槽 */
    ::-webkit-scrollbar-track {
        border-radius:5px;
    }

    /* 滚动条滑块 */
    ::-webkit-scrollbar-thumb {
        border-radius:10px;
        background: #e8eaf1;
    }
    .img-head{
        margin-top: 20px;
    }
    .blog{
        width: 600px;
        height: 100px;
        overflow: hidden;
        padding: 20px;
        border-top: 1px solid #e8eaf1;


        transition: 1s;
    }
    .blog:hover{
        background-color: #f9f9f9;
    }
    .blog:last-child{
        border-bottom:  1px solid #e8eaf1;
    }
    .blog span{
        margin-bottom: 4px;
        color: #8a8a8a;
        font-size: 14px;
        line-height: 24px;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        white-space: normal;
    }
    .blog a{
        max-width: 98%;
        color: #3d3d3d;
        display: block;
        line-height: 24px;
        height: 24px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        font-weight: bold;
    }
    .blog a:hover{
        color: #f40;
    }
    .ho span{
        cursor: pointer;
    }
    .ho span:hover{
        color: #f40 !important;
    }
    .up-blog{
        position: relative;
        border-bottom: 1px solid #e8eaf1;
        padding: 10px;
        margin-top: 5px;
    }
    .up-blog:last-child{
        border-bottom: none;
    }
    .up-blog .author{
        position: absolute;
        top: 8px;
    }
    .up-blog .author a{
        display: inline-block;
        margin-left: 60px;
        font-weight: bold;
        color: #3c3c3c;
    }
    .up-blog .author img{
        display: inline-block;
        width: 20px;
        height: 20px;
        margin-right: 5px;

    }
    .up-blog .author a:hover{
        color: #f40;
    }
    .up-blog .author .zan{
        position: absolute;
        left: 60px;
        height: 40px;
        font-size: 13px;
        color: #3c3c3c;
        line-height: 40px;
    }
    .zhibiao{
        display: inline-block;
        position: absolute;
        right: 10px;
    }
    .zhibiao span{

        height: 24px;
        text-align: right;
        font-size: 16px;
        font-weight: 600;
        color: #fc5531;
        line-height: 24px;
        position: relative;
        padding-right: 3px;
    }
    .zhibiao p{
        font-size: 12px;
        color: #999;
        line-height: 17px;
        text-align: right;
    }
</style>
