import request from '@/utils/request'
import baseURL from '@/api/global_variable'
//展示banner
export function listdisplay(id) {
  return request({
    url: baseURL + '/banner/display',
    method: 'get',
  })
}
//首页课程列表
export function courselist() {
  return request({
    url: baseURL + '/course/list',
    method: 'get',
  })
}
//新上课程
export function newcourselist() {
  return request({
    url: baseURL + '/course/list',
    method: 'get',
    params:{
      orderBy:'date'
    }
  })
}
//课程详细信息
export function getCourseInfo(id) {
  return request({
    url: baseURL + '/course/getCourseInfo',
    method: 'get',
    params:{
      courseId:id
    }
  })
}
//login
export function adminlogin(username,password) {
  return request({
    url: baseURL + '/login',
    method: 'post',
    params: {
      username:username,
      password:password
    }
  })
}
//评论
export function educomment(data) {
  return request({
    url: baseURL + '/edu-comment/comment',
    method: 'post',
    data:data
  })
}
//获取评论列表
export function listByCourseId(id) {
  return request({
    url: baseURL + '/edu-comment/listByCourseId',
    method: 'get',
    params: {
      courseId:id
    }
  })
}
//教育讯息
//admin公告列表
export function eduNoticeList(data) {
  return request({
    url: baseURL + '/edu-notice/list',
    method: 'get',
    params:data
  })
}
//留言
export function eduleavenmsginsert(data) {
  return request({
    url: baseURL + '/edu-leaven-msg/insert',
    method: 'post',
    data:data
  })
}
//签到
export function courseitemsigned(data) {
  return request({
    url: baseURL + '/course-item/signed',
    method: 'post',
    data:data,
  })
}
//判断签到状态
export function getStatus(courseId,userId) {
  return request({
    url: baseURL + '/course-item/getStatus',
    method: 'get',
    params:{
      courseId:courseId,
      userId:userId,
    }
  })
}
